.unify {
  background-color: #293037; }

.user-session {
  margin-right: 60px !important; }

.unify-logo {
  margin-left: 30px !important; }

.btn-unify {
  background: linear-gradient(0deg, #5da407 0%, #98c63d 100%);
  color: white !important;
  margin-left: 5px !important; }

.menu-unify {
  margin-top: 10px; }

.btn-unify:hover {
  text-decoration: underline; }

.unify-text {
  font-size: .9em;
  color: #5da407; }

.main {
  background: -webkit-linear-gradient(top, #cdcdcd, white);
  height: 100vh !important; }

.footer-unify {
  bottom: 1px;
  position: absolute;
  width: 100%;
  background-color: #293037; }

.path li {
  display: inline-block;
  padding-right: 4px;
  font-size: 1.2em; }
  .path li a:hover {
    text-decoration: underline; }

#currentpath {
  font-size: 1.2em;
  font-weight: bold;
  padding-right: 5px; }

.checkbox {
  margin-left: 50px; }

[type="checkbox"].filled-in:checked + label:after {
  border: 2px solid #5da407;
  background-color: #5da407; }

.dashboard-statusFiles {
  font-size: 1.2em; }

i.fa {
  margin-right: 15px; }

.container-unify {
  background-color: #fff; }

table.highlight tbody tr:hover {
  background-color: #cfe9c8 !important; }
